* {
  box-sizing: border-box;
}

html, body, .row {
    height: 100%;
    font-family: 'Helvetica', sans-serif;
}
/* Navbar */
div#navbar h1{
font-family: 'Sue Ellen Francisco', cursive;
font-size: 2em !important;
color: #f9f9f9 !important;
padding-top: 17.5px;
}
div#navbar {
  height: 35px;
  line-height: 35px;
  margin-bottom: 5%;
}
/* Calendar*/
div#month {
  margin:auto;
  display: inline-block;
  width: 100%;
  height: 75vh;
}
.rbc-calendar *,
.rbc-calendar *:before,
.rbc-calendar *:after {
}
div.rbc-day-bg {
  border-radius: 10%;
  background-color: #eee;
  margin: 20px;
  min-width: 50px;
  min-height: 50px;
}
div.rbc-day-bg:hover {
 background-color: #d7c770;
}
.rbc-month-view{
  border: 0;
}
.rbc-month-row + .rbc-month-row {
  height: 150px;
  min-width: 250px;
  min-height: 50px;
  border: 0;
}
.rbc-month-view, .rbc-header, .rbc-header
.rbc-day-bg, .rbc-day-bg {
  border-left: 0 !important;
  border-bottom: 0 !important;
}
.rbc-date-cell.rbc-now {
    font-weight: bold;
    color: #40c8e0;
}
.rbc-day-bg {
    color: #e0a840;
}
.rbc-row.rbc-month-header {
    color: #a9a9a9;
}
.rbc-toolbar .rbc-toolbar-label {
    width: 100%;
    padding: 0 10px;
    text-align: right;
    font-weight: 700;
    margin-right: 1%;
    color: #40c8e0;
    font-family: 'Sue Ellen Francisco', cursive;
    font-size: 1.5em;
}
.rbc-date-cell {
    margin: auto;
    justify-content: center;
    display: inline-flex;
    font-size: 20px;
    flex-basis: auto;
    text-align: center;
    min-height: 100px;
    align-self: center;
    max-height: 100px;
    align-items: center;
    vertical-align: middle;
}
.rbc-date-cell:hover {
  /*color: #e05840;*/
  /*transition: all .2s ease-in-out;
  transform: scale(1.33);*/
}
.rbc-toolbar button {
  font-size: 1em;
  border: 0;
}
div#shows {
  text-align: left;
}
div#show div button{
  text-align: right;
}

span.date-time-title{
  display: inline-block;
}
button.ticket-link, a.venue-link{
  display: inline-block;
}
div#showfeed div{
  /*background: #f9f9f9;*/
  text-align: left;
}
div#showfeed > span{
  margin: 5% 0 5% 0;
}
div.card-action {
    text-align: right;
}
div.card-action div {
    background: #000;
    color: #fff !important;
}
div#shows div.card-action div {
    text-align: right;
}
button.maybe-button div {
  background: #e0a840 !important;
  color: #000 !important;
}
button.go-button div {
  background: #40e0a8 !important;
  color: #000 !important;
}
/*Usersdash*/
div.row.ud-row-one, div.row.ud-row-two {
  margin: 50px 0 20px 0;
}
div.ud-followers-badge {
  display: inline-block;

}
div.six.columns {
background-color: #eee;
margin-top: 10%;
}
div#ud-maybes-follow-badge-go span {
    background-color: rgb(64,224,168) !important;
}
div#ud-going-follow-badge-go span {
    background-color: rgb(64,224,168) !important;
}
div#ud-maybes-follow-badge-maybe span {
    background-color: rgb(224,168,64) !important;
}
div#ud-going-follow-badge-maybe span {
    background-color: rgb(224,168,64) !important;
}
div#card-head-attend {
  font-weight: 600;
  text-transform: uppercase;
  background-color: #ccc !important;
}
div#card-head-maybe {
  font-weight: 600;
  text-transform: uppercase;
  background-color: #ccc !important;
}
div#card-action-attend {

  font-weight: 500;
  text-transform: uppercase;
  border-bottom: solid 1px #ccc;
}
div#card-action-maybe{
  font-weight: 500;
  text-transform: uppercase;
  border-bottom: solid 1px #ccc;
}
div#card-text-attend span{
  font-weight: 600;
}
div#card-text-maybe span{
  font-weight: 600;
}

/* login / signup */

div#Auth{
  width: 100%;
  height: 100%;
  overflow: hidden;
}
div#overlay {
  /*top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  position: sticky;
  background-color: rgba(64,224,168,0.5);
  background-size: cover;
  z-index: 1 !important;*/
}
div#login {
  postion: absolute;
  margin: 0 auto;
  margin-left: 39%;
  height: 100%;
  line-height: 100%;
  padding-bottom: 25px;
}
div#signup {
  postion: absolute;
  margin: 0 auto;
  margin-left: 39% !important;
  height: 100%;
  line-height: 100%;
}
div.three.columns {
  top: 25%;
  opacity: 0.925;
  background-color: #fff;
  margin-top: -25%;
  position: absolute;
  margin-bottom: 20px;
  display: block;
  padding-bottom: 25px;
  padding-top: 25px;
  margin: 0 auto;
  line-height: 100%;
}
div.four.columns.sign {
  top: 10.5%;
  opacity: 0.95;
  background-color: #fff;
  margin-top: -25%;
  position: absolute;
  margin-bottom: 20px;
  display: block;
  padding-top: 15px;
  margin: 0 auto;
  line-height: 100%;
}
div.twelve.columns {
  padding: 0;
  margin-bottom: 2%;
}
div.twelve.columns.sign {
  padding-top: 5px;
}
div.twelve.columns.sign {
padding-top: 2px;
}
label {
  padding: 0;
  text-transform: uppercase;
  font-size: 0.5em;
  font-weight: bolder;
}
button.button.six.columns.offset-by-three {
  background-color: #000;
  color: white;
}
button.button.six.columns.offset-by-three.backto {
  font-size: 8px;
  text-align: center;
  padding: 0;
}
button.button.six.columns.offset-by-three.btn {
  font-size: 8px;
  text-align: center;
  padding: 0;
}

/* background poster images / auth */
img#image {
  display: inline-flex;
  width: 100%;
  height: 100%;
}

img {
  box-sizing: border-box;
  width: 100%  !important;
  height: 100% !important;
}

#masonry-column-container {
    box-sizing: border-box;
    break-inside: avoid;
    margin: 0;
    padding: 0;
    column-count: 6;
    column-fill: auto;
    column-gap: 1.5em;
}

.brick-item {
    display: inline-block;
    background: #eee;
    padding: 1em;
    margin: 0 0 1.5em;
    width: 100%;
    flex-shrink: 1 0 0;
    box-sizing: border-box;
    box-shadow: 2px 2px 4px 0 #bbb;
}
div#wrapper {
  /*transform: translateY(4%);*/
}

/* MEDIA QUERIES */
@media (min-width: 768px) and (max-width: 1046px) {
  /* POSTERS */
  #masonry-column-container {
    width: 150%;
    box-sizing: border-box;
    break-inside: avoid;
    margin: 0;
    padding: 0;
    column-count: 6;
    column-fill: auto;
    column-gap: 0.5em;
  }
  div#wrapper {
    transform: translateY(3%);
  }
  /* LOGIN */
  div.three.columns {
    top:172.5px;
    margin: 0 auto;
    padding: 5% 0;
    width: 33%;
  }
  div#login {
    margin-left: 33%;
  }
  /* SIGNUP */
  div#signup {
    postion: absolute;
    margin: 0 auto;
    margin-left: 39% !important;
    height: 100%;
    line-height: 100%;
    width: 50%;
  }
  div.four.columns.sign {
    top: 12.5%;
    margin-top: -25%;
    position: absolute;
    padding-top: 15px;
    margin: 0 auto;
    line-height: 100%;
    padding: 0;
  }
}
@media (min-width: 550px) and (max-width: 767px) {
  /* Posters */
  #masonry-column-container {
      width: 200%;
      box-sizing: border-box;
      break-inside: avoid;
      margin: 0;
      padding: 0;
      column-count: 6;
      column-fill: auto;
      column-gap: 0.5em;
  }
  div#wrapper {
    transform: translateY(3%);

  }
  /* LOGIN */
  div.three.columns {
    top:33%;
    margin: 0 auto;
    padding: 2 0 2 0;
    width: 45%;
  }

  div#login {
    margin-left: 27.5%;
  }
  /* SIGNUP */
  div#signup {
    postion: absolute;
    margin: 0 auto;
    height: 100%;
    line-height: 100%;
    width: 65%;
  }
  div.four.columns.sign {
    margin-left: -7.5% !important;
    padding: 4.5%;
    top: 12.5%;
    margin-top: -25%;
    position: absolute;
    padding-top: 15px;
    margin: 0 auto;
    line-height: 100%;
    padding: 0;
    width: 45%;
  }
}


@media (min-width: 200px) and (max-width: 549px) {
  /* POSTERS */
  #masonry-column-container {
      margin-left: 9px;
      width: 300%;
      column-count: 6;
      column-gap: 1em;
  }
  div#wrapper {
    transform: translateY(2.5%);
  }

  /* LOGIN */
  div.three.columns {
    top:25%;
    padding: 10%;
    width: 50%;
  }
  div.twelve.columns {
    padding: 0;
    margin-bottom: 2%;
  }
  div#login {
    margin-left: 28%;
  }
  /* SIGNUP */
  div#signup {
    postion: absolute;
    width: 50%;
  }
  div.four.columns.sign {
    margin-left: 20% !important;
    top: 10%;
    margin-top: -25%;
    position: absolute;
    padding-top: 15px;
    padding-bottom: 15px;
    margin: 0 auto;
    line-height: 100%;
    padding: 7.5%;
    width: 60%;
  }

}
